___CSS_LOADER_EXPORT___.push([module.id, `* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,
body {
  width: 100vw;
  height: 100vh;
}

.app {
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-rows: 100px 1fr;
}

header {
  display: grid;
  grid-template-columns: repeat(8, 1fr);
  grid-template-rows: 50px 25px 25px;
  margin-top: 20px;
}

header h1 {
  grid-column: 1 / -1;
  text-align: center;
}

header #search {
  grid-column: 4 / 5;
}

header #searchBtn {
  width: 80%;
  justify-self: center;
}

#weatherImg {
  width: 80px;
  height: 80px;
  background-position: center;
  background-size: cover;
}

main {
  display: flex;
  flex-direction: column;
  align-items: center;
}

#conditionText {
  margin-bottom: 15px;
}

.display {
  margin-top: 100px;
  width: 500px;
  border-radius: 8px;
  box-shadow: 0px 0px 5px 5px rgb(0, 0, 0, 0.33);
  padding: 25px;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.temperature {
  display: flex;
  width: 100%;
  justify-content: space-around;
  margin-top: 25px;
  text-align: center;
  font-size: 26px;
}

#temp-actual,
#temp-feelslike {
  font-size: 26px;
  font-weight: bold;
  margin-top: 10px;
}

.other-data {
  margin-top: 20px;
  font-size: 18px;
  font-weight: bold;
  width: 70%;
  display: flex;
  justify-content: space-between;
  gap: 20px;
  border-bottom: 1px dashed black;
}

.toggle {
  margin-top: 25px;
}

.F,
.C {
  font-size: 28px;
  font-weight: bold;
  padding: 25px;
  width: 100px;
  border: none;
  border-radius: 8px;
  background-color: lightseagreen;
  color: white;
  margin-right: 25px;
  margin-left: 25px;
  box-shadow: 4px 4px 4px rgb(0, 0, 0, 0.33);
}

.F:hover,
.C:hover {
  transform: scale(0.95);
  cursor: pointer;
}

.activeBtn {
  color: lightseagreen;
  background-color: white;
  border: 2px solid lightseagreen;
  box-shadow: inset 4px 4px 4px rgb(0, 0, 0, 0.33);
}

.activeBtn:hover {
  transform: none;
}
